import { HttpErrorResponse } from '@angular/common/http';
import { PaymentOrdersPost } from '@backbase/data-ang/payment-order';
import { createAction, props } from '@ngrx/store';
import { Arrangement, Contact, ExternalAccount } from '../models/items.model';
import { ViewState } from '../models/view.model';

export const accountsFetch = createAction('[Accounts API] fetching accounts');

export const contactsFetch = createAction('[Contacts API] fetching contacts');

export const externalAccountsFetch = createAction('[Payments API] fetching external accounts');

// Success
export const accountsFetchSuccess = createAction(
  '[Accounts API] accounts fetched success',
  props<{ debitAccounts: Arrangement[]; creditAccounts: Arrangement[] }>(),
);

export const contactsFetchSuccess = createAction(
  '[Contacts API] contacts fetched success',
  props<{ contacts: Contact[] }>(),
);

export const extAccountsFetchSuccess = createAction(
  '[Payments API] external accounts fetched success',
  props<{ extAccounts: ExternalAccount[] | undefined }>(),
);

// Error
export const accountsFetchError = createAction(
  '[Accounts API] accounts fetching error',
  props<{ error: HttpErrorResponse }>(),
);

export const contactsFetchError = createAction(
  '[Contacts API] contacts fetching error',
  props<{ error: HttpErrorResponse }>(),
);

export const extAccountsFetchError = createAction(
  '[Payments API] external accounts fetching error',
  props<{ error: HttpErrorResponse }>(),
);

export const openView = createAction(
  '[Quick Transfer Widget] current view changed',
  props<{ view: ViewState; focus?: string }>(),
);

export const setPayeeAccount = createAction('[Quick Transfer Widget] payee account picked up', props<{ id: string }>());

export const setFromAccount = createAction(
  '[Quick Transfer Widget] From Account changed',
  props<{ accountId: string }>(),
);

export const sendTransferOrder = createAction(
  '[Payments Order API] send transfer order',
  props<{ reqData: PaymentOrdersPost }>(),
);
export const transferOrderSuccess = createAction('[Payments Order API] transfer order is successful');

export const transferOrderError = createAction(
  '[Payments Order API] transfer order is failed',
  props<{ error: HttpErrorResponse }>(),
);

export const setAmount = createAction(
  '[Quick Transfer Widget] Transfer Amount Set',
  props<{ amount: string; defaultCurrency?: string }>(),
);

export const setModalOpen = createAction(
  '[Quick Transfer Widget] Confirmation Modal Open',
  props<{ isOpen: boolean }>(),
);

export const resetWidget = createAction('[Quick Transfer Widget] Reset widget', props<{ focus?: string }>());

export const resetFocus = createAction('[Quick Transfer Widget] Reset Focus Element');

export const setFieldTouched = createAction(
  '[Quick Transfer Widget] Set Field Touched',
  props<{ fieldName: string }>(),
);

export const setDefaultCurrency = createAction(
  '[Quick Transfer Widget] Default Currency changed',
  props<{ currency: string }>(),
);
